"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { Tabs, TabsList, TabsTrigger } from "@/components/shadcn/ui/tabs"

export default function Layout({ children }: { children: React.ReactNode }) {
  const pathname = usePathname()

  const tabValue = pathname.includes("/saved")
    ? "2"
    : pathname.includes("/discover")
    ? "3"
    : "1"

  return (
    <Tabs value={tabValue} className="w-full">
      <TabsList className="grid w-[400px] grid-cols-3">
        <TabsTrigger value="1" asChild>
          <Link href="/education/english/vocabulary/list/my">Thư viện của tôi</Link>
        </TabsTrigger>
        <TabsTrigger value="2" asChild>
          <Link href="/education/english/vocabulary/list/saved">Đã lưu</Link>
        </TabsTrigger>
        <TabsTrigger value="3" asChild>
          <Link href="/education/english/vocabulary/list/discover">Khám phá</Link>
        </TabsTrigger>
      </TabsList>

      <div className="mt-4">{children}</div>
    </Tabs>
  )
}
